const jsonString = JSON.stringify;
const jsonStringWithUndefined = (obj) =>
  jsonString(obj, (_key, value) => (value === void 0 ? UNDEFINED : value));

const stampNewObjectWithHash = () => stampNewWithHash({}, EMPTY_STRING, 0);
